body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  list-style: none;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 90px;
  margin: 50px 0;
}

.form__title {
  font-size: 36px;
}

.form__input {
  height: 25px;
  width: 30%;
  text-align: center;
}
.pokemon__card {
  width: 200px;
  height: 250px;
  background-color: white;
  border: 1px solid grey;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
}

.image__container {
  background-color: lightgray;
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.pokemon__card-image {
  align-self: center;
}

.data__name {
  margin-left: 10px;
  text-transform: capitalize;
}

.data__types {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 10px;
}

.data__types-type {
  border: 1px solid #9E9E9E;
  border-radius: 10px;
  display: block;
  margin-right: 10px;
  padding: 5px;
}


.pokemon__list {
  display: flex;
    flex-flow: row wrap;
    list-style: none;
    margin: 0 30px;
}
